"use client"

import { motion } from "framer-motion"
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { ExternalLink, Github } from 'lucide-react'

const projects = [
  {
    title: "Lumol â€“ Ad-tech platform for conversational ads (Completed)",
    description: "Lumol is an ad-tech platform that connects advertisers with LLM providers for conversational ad placements. I designed the platform to be minimalist and modern, with light, mid, and dark mode themes. The interface emphasizes clarity, usability, and seamless navigation, ensuring advertisers and AI providers can collaborate efficiently.",
    role: "Lead UI/UX Designer",
    tools: ["Figma", "Adobe XD", "Jira"],
    technologies: ["Next.js 14", "TypeScript", "React 18", "ESLint", "CSS"],
    view: "https://lumol.com", // Replace with your project link
  },
  {
    title: "Project 2",
    description: "A brief description of your second project",
    role: "Frontend Developer",
    tools: ["VS Code", "Postman"],
    technologies: ["Node.js", "Express", "MongoDB"],
    view: null,
  },

  {
    title: "Project 2",
    description: "A brief description of your second project",
    role: "Frontend Developer",
    tools: ["VS Code", "Postman"],
    technologies: ["Node.js", "Express", "MongoDB"],
    view: null,
  },

  {
    title: "Project 2",
    description: "A brief description of your second project",
    role: "Frontend Developer",
    tools: ["VS Code", "Postman"],
    technologies: ["Node.js", "Express", "MongoDB"],
    view: null,
  },
]

export default function Projects() {
  return (
    <div className="container py-12">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="space-y-6"
      >
        <h1 className="text-3xl font-bold ml-5">Projects</h1>
        {projects.map((project, index) => (
          <Card key={index} className="mb-6">
            <CardHeader>
              <CardTitle>{project.title}</CardTitle>
            </CardHeader>
            <CardContent>
              <p>{project.description}</p>
              <div className="mt-4">
                <span className="font-semibold">Role:</span> {project.role}
              </div>
              <div className="mt-2">
                <span className="font-semibold">Tools Used:</span>
                <div className="flex flex-wrap gap-2 mt-1">
                  {project.tools.map((tool, idx) => (
                    <Badge key={idx}>{tool}</Badge>
                  ))}
                </div>
              </div>
              <div className="flex flex-wrap gap-2 mt-4">
                {project.technologies.map((tech, idx) => (
                  <Badge key={idx}>{tech}</Badge>
                ))}
              </div>
            </CardContent>
            <CardFooter className="flex justify-end">
              {project.view ? (
                <a href={project.view} target="_blank" rel="noopener noreferrer">
                  <Button variant="outline">
                    <ExternalLink className="mr-2 h-4 w-4" /> View Project
                  </Button>
                </a>
              ) : (
                <Button variant="outline" disabled>
                  <ExternalLink className="mr-2 h-4 w-4" /> Project Not Available
                </Button>
              )}
            </CardFooter>
          </Card>
        ))}
      </motion.div>
    </div>
  )
}